/* CountryRegionCitySelect.css */

.select-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-family: 'Helvetica Neue', sans-serif;
}

.select-container input {
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #3a7bd5;
  border-radius: 10px;
  outline: none;
}

.select-container input::placeholder {
  color: #3a7bd5;
}

.select-container input:focus {
  border-color: #3a7bd5;
  box-shadow: 0 0 10px #3a7bd5;
}


@media only screen and (max-width: 600px) {
  .select-container input {
      font-size: 14px;
  }
}
